nav {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.menu {
  display: flex;

  align-items: center;
  height: 100%;
  margin-right: 30px;
  padding-right: 30px;
  padding-top: 2px;
  box-sizing: border-box;
}

.menu li {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 14px;
  letter-spacing: 0px;
}

.nav-link {
  display: inline-block;
  text-decoration: none;
}
.nav-link:after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: $thirdly;
  transition: width 0.3s;
}
.nav-link:hover::after {
  width: 100%;
  transition: width 0.5s;
}

@media (max-width: 769px) {
  .menu {
    display: none;
  }
}
